<template>
  <div ref="formObject">
    <h2 class="subtitle"></h2>
    <Form ref="orderFrom" class="orderFrom" v-slot="{ errors }" @submit="sendOrder">
      <div class="mb-3">
        <label for="name" class="form-label"></label>
        <Field type="text"
              class="form-control"
              id="name"
              placeholder=""
              name=""
              v-model="user.name"
              rules="required"
              :class="{ 'is-invalid': errors[''] }"
        ></Field>
        <ErrorMessage name="" class="invalid-feedback"></ErrorMessage>
      </div>
      <div class="mb-3">
        <label for="email" class="form-label">Email</label>
        <Field type="email"
              class="form-control"
              id="email"
              placeholder="Email@xxx.xxx"
              name="Email"
              v-model="user.email"
              rules="email|required"
              :class="{ 'is-invalid': errors['Email'] }"
        ></Field>
        <ErrorMessage name="Email" class="invalid-feedback"></ErrorMessage>
      </div>
      <div class="mb-3">
        <label for="tel" class="form-label"></label>
        <Field type="number"
              class="form-control"
              id="tel"
              placeholder="0900000000"
              name=""
              v-model="user.tel"
              rules="min:10|required"
              :class="{ 'is-invalid': errors[''] }"
        ></Field>
        <ErrorMessage name="" class="invalid-feedback"></ErrorMessage>
      </div>
      <div class="mb-3">
        <label for="address" class="form-label"></label>
        <Field type="text"
              class="form-control"
              id="address"
              placeholder=""
              name=""
              v-model="user.address"
              rules="required"
              :class="{ 'is-invalid': errors[''] }"
        ></Field>
        <ErrorMessage name="" class="invalid-feedback"></ErrorMessage>
      </div>
      <div class="mb-3">
        <label for="pay" class="form-label"></label>
        <Field
          id="pay"
          class="form-select"
          name=""
          v-model="user.pay"
          rules="required"
          :class="{ 'is-invalid': errors[''] }"
          as="select"
        >
          <option value="" selected disabled></option>
          <option value="ATM">ATM</option>
          <option value=""></option>
        </Field>
        <ErrorMessage name="" class="invalid-feedback"></ErrorMessage>
      </div>
      <div class="mb-3">
        <label for="message" class="form-label"></label>
        <textarea
          class="form-control"
          id="message"
          rows="6"
          v-model="message"
        ></textarea>
      </div>
      <button class="btn btn-secondary w-100 px-3"></button>
    </Form>
  </div>
</template>

<script>
import Collapse from 'bootstrap/js/dist/collapse';

export default {
  data() {
    return {
      form: '',
      user: {},
      message: '',
    };
  },
  methods: {
    // 
    openForm() {
      this.form.show();
    },
    // 
    closeForm() {
      this.form.hide();
    },
    // 
    sendOrder() {
      const orderData = {
        user: this.user,
        message: this.message,
      };
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/order`;
      this.$http.post(api, { data: orderData }).then((res) => {
        if (res.data.success) {
          this.$swal.fire({
            icon: 'success',
            title: res.data.message,
          });
          this.$refs.orderFrom.resetForm();
          this.message = '';
          this.$emit('renderCart');
          this.$emit('renderCartNum');
          this.closeForm();
        } else {
          this.$swal.fire({
            icon: 'error',
            title: res.data.message,
          });
        }
      }).catch((err) => {
        console.log(err);
      });
    },
  },
  mounted() {
    this.form = new Collapse(this.$refs.formObject, {
      toggle: false,
    });
  },
};
</script>
