<template>
  <section class="subscription">
    <div class="container subscription-content">
      <label for="subscriptionEmail"> </label>
      <Form class="input-group" v-slot="{ errors }" @submit="onSubmit">
        <Field
          type="email"
          id="subscriptionEmail"
          class="form-control"
          name="email"
          placeholder=""
          v-model="userEmail"
          rules="email"
          :class="{ 'is-invalid': errors['email'] }"
        ></Field>
        <button type="submit" class="input-group-text"></button>
        <ErrorMessage name="email" class="invalid-feedback"></ErrorMessage>
      </Form>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      userEmail: '',
      subscriptionEmails: [],
    };
  },
  methods: {
    onSubmit() {
      if (this.userEmail === '') {
        this.$swal.fire({
          icon: 'error',
          title: ' Email ',
        });
      } else {
        this.subscriptionEmails.push(this.userEmail);
        this.$swal.fire({
          icon: 'success',
          title: '',
        });
        this.userEmail = '';
      }
    },
  },
};
</script>
